.ReactTable .rt-tbody {
  max-height: 300px;
  overflow: auto;
}
.ReactTable.is-horizontal .rt-thead.-header {
  display: none;
}
.ReactTable .rt-thead.-header {
  background-color: #e6fff2;
}
.ReactTable .rt-thead .rt-resizable-header-content,
.ReactTable .rt-tbody .rt-td {
  text-align: start;
  padding: 10px;
}
.ReactTable .-pagination .-btn {
  color: #fff;
  background-color: #292525;
}
.ReactTable .rt-tbody .rt-tr:hover .actions {
  opacity: 1 !important;
}
.actions {
  opacity: 0;
  display: flex;
  justify-content: space-around;
}
.actions > img {
  cursor: pointer;
  width: 35px;
}
